<script>
export default {
  props: {
    type: {
      type: String,
      default: "normal"
    },
    text: {
      type: String,
      default: "button"
    }
  },
  render(createElement) {
    /*
            h  createElement 2
            1 - 
            2 -  
         */
    return createElement("button", {
      //  v-bind:class
      class: {
        btn: true,
        "btn-success": this.type === "success",
        "btn-danger": this.type === "danger",
        "btn-warning": this.type === "warning",
        "btn-normal": this.type === "normal"
      },
      domProps: {
        innerText: this.text || ""
      },
      on: {
        click: this.handleClick
      }
    });
  },
  methods: {
    handleClick() {
      this.$emit("myClick");
    }
  }
};
</script>

<style scoped>
.btn {
  width: 100px;
  height: 40px;
  line-height: 40px;
  border: 0px;
  border-radius: 5px;
  color: #ffff;
  outline: none;
  transition: all 0.2s ease-in-out;
}
.btn:hover{
    cursor: pointer;
}
.btn:active{
    opacity: 0.8;
}
.btn-success {
  background: #2ecc71;
}
.btn-danger {
  background: #e74c3c;
}
.btn-warning {
  background: #f39c12;
}
.btn-normal {
  background: #bdc3c7;
}
</style>



