<template>
  <div>
    <ul class="box">
      <li
        v-for="(item, index) in computeNumber"
        :key="index"
        :class="{ 'number-item': !isNaN(item) }"
      >
        <!-- {{ !isNaN(item) true}} -->
        <span v-if="!isNaN(item)" id="Number">
          <i ref="numberItem">0123456789</i>
        </span>
        <span v-else class="dou">{{ item }}</span>
      </li>
    </ul>
    <scroll-digital :time="1" :value="allCount"></scroll-digital>
  </div>
</template>

<script>
import scrollDigital from "@/components/scrollDigital";

export default {
  data() {
    return {
      computeNumber: ["0", "0", "0", "0", "0", "9", "1", "7"],
      allCount: 300
    };
  },
  created() {},
  methods: {
    // 
    increaseNumber() {
      let self = this;
      this.timer = setInterval(() => {
        self.newNumber = self.newNumber + getRandomNumber(1, 100);
        self.setNumberTransform();
      }, 3000);
    },
    // 
    setNumberTransform() {
      let numberItems = this.$refs.numberItem;
      let numberArr = this.computeNumber.filter(item => !isNaN(item));
      for (let index = 0; index < numberItems.length; index++) {
        let elem = numberItems[index];
        elem.style.transform = `translate(-50%, -${numberArr[index] * 10}%)`;
      }
    }
  },
  mounted() {
    this.increaseNumber();
  }
};

function getRandomNumber(min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min);
}
</script>

<style>
.box {
  overflow: hidden;
}
.box li .dou {
  display: inline-block;
  font-size: 30px;
  height: 60px;
  line-height: 80px;
  color: #ff6744;
}
li {
  float: left;
}
.number-item {
  width: 35px;
  height: 45px;
  float: left;
  overflow: hidden;
  margin: 0 2px;
}
.number-item span {
  position: relative;
  display: inline-block;
  /* margin-right: 10px; */
  width: 100%;
  height: 100%;
  writing-mode: vertical-rl;
  text-orientation: upright;
  overflow: hidden;
  color: #ff6744;
  font-size: 40px;
}
.number-item span i {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  transition: transform 0.5s ease-in-out;
  letter-spacing: 10px;
}
</style>



