<template>
  <div class="shake">
    <el-row v-if="show" class="shake-box">
      <el-col :xs="8" :sm="6" :md="4" :lg="3" :xl="1">
        <!-- <el-button type="primary" id="btn" @click="throttle()"></el-button> -->
        <el-button type="primary" id="btn" @click="login"></el-button>
        <div v-if="show" class="box">
          <div class="content">1</div>
          <div class="content">2</div>
          <div class="content">3</div>
          <div class="content">4</div>
          <div class="content">5</div>
          <div class="content">6</div>
        </div>
      </el-col>
    </el-row>
    <div
      v-show="show"
      class="btn-text"
      style="letter-spacing:12px;"
      type="primary"
    >
      <span></span>
    </div>
    <div
      v-show="show"
      style="width:120px;line-height:20px;border:1px solid skyblue;letter-spacing:28px;"
    >
      
    </div>
    <p v-show="show" class="article">
      
    </p>
    <p v-show="show" class="article2">
      
    </p>
    <el-row>
      <el-col :sm="24">
        <template>
          <transition name="fade">
            <el-table v-if="!show" :data="tableData1" border>
              <!-- -->
              <el-table-column
                v-for="(item, index) in tableTitle"
                :key="index"
                :label="item"
              >
                <!--   scope.row-->
                <template slot-scope="scope">
                  <span v-if="scope.row.list[index].isSet">{{
                    scope.row.list[index].value
                  }}</span>
                  <el-input
                    v-else
                    :value="scope.row.list[index].value"
                  ></el-input>
                </template>
              </el-table-column>
            </el-table>
          </transition>
          <el-form
            :model="numberValidateForm"
            ref="numberValidateForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item>
              <el-button
                type="primary"
                @click="submitForm('numberValidateForm')"
                ></el-button
              >
              <el-button @click="resetForm('numberValidateForm')"
                ></el-button
              >
              <el-button @click="toggle"></el-button>
            </el-form-item>
          </el-form>
        </template>
        <table
          style="margin-top:20px;margin-left:1%;width: 98%;border:1px solid #EBEEF5;border-collapse: collapse;"
          v-if="show"
          border
          cellspacing="0"
          cellpadding="0"
        >
          <tr>
            <td
              class="cell-content"
              v-for="(item, index) in tableTitle"
              :key="index"
            >
              {{ item }}
            </td>
          </tr>
          <tr
            class="cell-header"
            v-for="(items, index) in tableData"
            :key="index"
          >
            <td
              class="cell-content"
              v-for="(item, index) in items"
              :key="index"
            >
              {{ item }}
            </td>
          </tr>
        </table>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import "@/utils/mock";
export default {
  data() {
    return {
      colunmName: [
        "--",
        "--1",
        "--2",
        "--3"
      ],
      data: [
        {
          list: [
            {
              name: "--",
              value: 0
            },
            {
              name: "--1",
              value: 1
            },
            {
              name: "--2",
              value: 2
            },
            {
              name: "--",
              value: 3
            }
          ]
        }
      ],
      show: false,
      msg: "shake content!",
      tableTitle: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      tableData1: [
        {
          list: [
            {
              name: "",
              value: "2016-05-02",
              isSet: true
            },
            {
              name: "",
              value: "",
              isSet: true
            },
            {
              name: "",
              value: "",
              isSet: true
            },
            {
              name: "",
              value: "",
              isSet: true
            },
            {
              name: "",
              value: " 1518 ",
              isSet: true
            },
            {
              name: "",
              value: 200333,
              isSet: true
            },
            {
              name: "",
              value: "",
              isSet: true
            },
            {
              name: "",
              value: "202042410:01:23",
              isSet: true
            }
          ]
        }
      ],
      tableData: [
        {
          date: "2016-05-02",
          name: "",
          province: "",
          city: "",
          address: " 1518 ",
          zip: 200333,
          tag: "",
          : "202042410:01:23"
        }
      ],
      numberValidateForm: {
        age: ""
      },
      isSet: true,
      dateVal: ""
    };
  },
  created() {
    //$('title').innerText=''
  },
  methods: {
    toggle() {
      this.show = !this.show;
    },
    validate() {
      if (this.dateVal === "") {
        this.$message.error("");
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
          this.isSet = false;
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.isSet = true;
      this.$refs[formName].resetFields();
    },
    submit() {},
    add() {
      this.tableData1.push({
        list: [
          {
            name: "",
            value: "2016-05-02",
            isSet: true
          },
          {
            name: "",
            value: "",
            isSet: true
          },
          {
            name: "",
            value: "",
            isSet: true
          },
          {
            name: "",
            value: "",
            isSet: true
          },
          {
            name: "",
            value: " 1518 ",
            isSet: true
          },
          {
            name: "",
            value: 200333,
            isSet: false
          },
          {
            name: "",
            value: "",
            isSet: true
          },
          {
            name: "",
            value: "202042410:01:23",
            isSet: true
          }
        ]
      });
    },
    Debounce(fn) {
      let timer;
      return function() {
        if (timer) {
          clearTimeout(timer);
        }
        timer = setTimeout(() => {
          timer = null;
          fn.apply(this, arguments);
        }, 500);
      };
    },
    throttle(i = 0) {
      // console.dir(document.getElementById("btn"));
      let weeksMap = new Map([
        [1, ""],
        [2, ""],
        [3, ""],
        [4, ""],
        [5, ""],
        [6, ""],
        [7, ""]
      ]);
      weeksMap.set(8, "");
      //console.count(weeksMap.get(i) ? "" + weeksMap.get(i) : "");
      var arr = [1, 6, 6, 9, 2, 6, 1, 3, "name", "sex"];
      var obj = {
        name: "lucy",
        sex: "male",
        age: "seven"
      };
      var newObj = {};
      var oKeys = Object.keys(obj);
      for (let i in arr) {
        for (let k in obj) {
          if (k === arr[i]) {
            newObj[k] = obj[k];
          }
        }
      }
      // console.log(newObj);
      // console.log(arr.includes(6));
      // console.log([...new Set(arr)]);
      // console.log(Array.from(new Set(arr)));
      // console.log(new Set("helloow"));
      var contentArr = new Map();
      contentArr.set(1, "key");
      console.log(...contentArr);
    },
    login() {
      let email = "my-user@163.com";
      this.$http.post("/api/login", { email }).then(response => {
        if (response.data.length === 0) {
          this.$message.error("");
        } else {
          this.$message.success(response.data[0].name + "");
        }
      });
    }
  }
};
</script>

<style>
.shake-box {
  margin: 20px;
}
.box {
  width: 300px;
  height: 200px;
  border: 1px solid #87ceeb;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}
.content {
  border: 1px solid hotpink;
  text-align: center;
  line-height: 100px;
}
.btn-text {
  width: 120px;
  height: 20px;
  line-height: 20px;
  border: 1px solid skyblue;
  text-align: justify;
  padding: 0 10px;
}
.btn-text span {
  display: inline-block;
  padding-left: 100%;
}
.article {
  width: 100px;
  height: 40px;
  border: 1px solid skyblue;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.article2 {
  width: 100px;
  border: 1px solid skyblue;
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.cell-content {
  padding: 10px 30px;
}
.cell-header:hover {
  background: #f5f7fa;
  /* color: #fff; */
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 2s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



