<template>
  <div class="login">
    <div v-if="show" class="login-input">
      <h1 class="login-title"></h1>
      <el-form
        class="login-form"
        ref="form"
        :model="userForm"
        label-width="60px"
      >
        <el-form-item label="">
          <el-input
            placeholder=""
            v-model="userForm.name"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            placeholder=""
            v-model="userForm.password"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="login-btn" type="primary"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!--  -->
    <!-- <input v-if="!show" v-model="inputVal" type="text" @keyup="debounce" /> -->
    <!-- <el-button type="primary" @click="throttle"></el-button> -->
    <el-button v-if="show" type="primary" @click="setTime"></el-button>
    <el-table v-if="show" id="out-table" :data="tableData" style="width: 100%">
      <el-table-column prop="date" label="" width="180"> </el-table-column>
      <el-table-column prop="name" label="" width="180"> </el-table-column>
      <el-table-column prop="address" label=""> </el-table-column>
    </el-table>
    <el-upload
      v-if="show"
      class="upload"
      action=""
      :multiple="false"
      :show-file-list="true"
      accept=".xls"
      :http-request="httpRequest"
    >
      <el-button v-if="show" size="small" type="primary"></el-button>
    </el-upload>
    <el-button v-if="show" @click="exportExcel" size="small" type="primary"
      ></el-button
    >
    <ul v-if="show">
      <li v-for="item in list" :key="item.id">
        <p>{{ item.name }}</p>
        <img :src="item.image" alt="" />
      </li>
    </ul>
    <!-- siderBar -->
    <el-col
      v-if="!show"
      style="height:100%;background-color:#052137;"
      :span="3"
    >
      <el-menu
        default-active="2"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        background-color="#052137"
        text-color="#fff"
        active-text-color="#35DDF6"
      >
        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-location"></i>
            <span></span>
          </template>
          <el-menu-item index="1-1">1</el-menu-item>
          <el-menu-item index="1-2">2</el-menu-item>
          <el-menu-item index="1-3">3</el-menu-item>
        </el-submenu>
        <el-menu-item index="2">
          <i class="el-icon-menu"></i>
          <span slot="title"></span>
        </el-menu-item>
      </el-menu>
    </el-col>
    <i v-if="inp" class="inp">*</i>&nbsp;<el-input
      ref="input"
      v-model="street"
    ></el-input>
    <p v-if="inp" class="inp">{{ mes }}</p>
    <el-button @click="sumbit" type="primary">submit</el-button>
    <div v-if="show" id="loginCard">
      <el-form
        class="loginForm"
        label-width="80px"
        ref="form"
        :model="loginForm"
      >
        
        <el-form-item label="">
          <el-input
            v-model="loginForm.name"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="loginForm.password"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import FileSaver from "file-saver";
import XLSX from "xlsx";
import "@/utils/mock";
// import schema from "@/utils/validate";
import schema from "async-validator";

let timer, lastTime;
let now = +new Date();
export default {
  name: "login",
  data() {
    return {
      mes: "",
      inp: false,
      tableData: [
        { date: "2020320", name: "zhangsan", address: "" }
      ],
      show: false,
      inputVal: "",
      userForm: {
        name: "",
        password: ""
      },
      loginForm: {
        name: "",
        password: ""
      },
      nowTime: new Date(),
      second: undefined,
      onceAll: 0,
      list: undefined,
      street: "",
      city: "",
      zip: ""
    };
  },
  created() {
    // console.log(this.$http);
    this.getData();
    // this.checkData2();
  },
  mounted() {
    // console.log(document.getElementsByTagName('style')[0].innerHTML)
  },
  watch: {
    street() {
      this.checkData2();
    }
  },
  methods: {
    checkData2() {
      var descriptor1 = {
        address: {
          type: "object",
          required: true,
          fields: {
            street: {
              type: "string",
              required: true,
              min: 5,
              max: 10,
              message: "street510"
            },
            city: { type: "string", required: true, message: "city" }
            // zip: {
            //   type: "string",
            //   required: true,
            //   len: 8,
            //   message: "zip"
            // }
          }
        },
        name: { type: "string", required: true }
      };
      var validator1 = new schema(descriptor1);
      validator1.validate(
        {
          address: { street: this.street, city: this.city },
          name: ""
        },
        (errors, fields) => {
          if (errors) {
            this.inp = true;
            // console.log(this.$refs.inp.value);
            errors.map(data => {
              this.mes = data.message;
              console.log(data.message);
              this.$refs.input.focus();
            });
          } else {
            this.inp = false;
            console.log("");
          }
          // errors for address.street, address.city, address.zip
        }
      );
    },
    getData() {
      var url = "/api/getprodlist";
      this.$http.get(url).then(response => {
        // console.log(response.data.data.list);
        this.list = response.data.data.list;
        console.log(this.list);
      });
    },
    downLoadExlce() {
      let hrefStr = encodeURI("");
      window.location.href = hrefStr;
    },
    httpRequest(e) {
      let file = e.file; // 
      if (!file) {
        // 
        return false;
      } else if (!/\.(xls|xlsx)$/.test(file.name.toLowerCase())) {
        // 
        this.$message.error("xlsxlsx");
        return false;
      }
      const fileReader = new FileReader();
      fileReader.onload = ev => {
        try {
          const data = ev.target.result;
          const workbook = XLSX.read(data, {
            type: "binary" // 
          });
          const exlname = workbook.SheetNames[0]; // 
          const exl = XLSX.utils.sheet_to_json(workbook.Sheets[exlname]); //json
          //  JSON  data 
          console.log(this.tableData);
          this.tableData = exl;
          // document.getElementsByName('file')[0].value = ''
          // value
        } catch (e) {
          console.log("");
          return false;
        }
      };
      fileReader.readAsBinaryString(file);
    },
    /*  */
    debounce() {
      let timer1;
      if (timer1) {
        clearTimeout(timer1);
      }
      timer1 = setTimeout(() => {
        console.log("");
        timer1 = undefined;
      }, 1000);
    },
    throttle() {
      if (lastTime && now - lastTime < 200) {
        clearTimeout(timer);
        console.log("....");
        timer = setTimeout(() => {
          console.log("...");
          lastTime = +new Date();
        }, 2000);
      } else {
        //
        lastTime = now;
        timer = setTimeout(() => {
          console.log("...111");
          lastTime = +new Date();
        }, 200);
      }
    },
    setTime() {
      let outTime = 0,
        outTimer;

      if (this.onceAll && this.nowTime - this.onceAll < 200) {
        clearTimeout(outTimer);
        console.log("----");
        outTimer = setTimeout(() => {
          console.log("--setTimeout");
          this.onceAll = +new Date();
        }, 2000);
      } else {
        this.onceAll = this.nowTime;
        outTimer = setTimeout(() => {
          console.log("--setTimeout1111");
          this.onceAll = +new Date();
        }, 500);
      }
      //   outTimer = setInterval(() => {
      //     if (outTime && outTime > 5) {
      //       clearInterval(outTimer);
      //     } else {
      //       outTime += 1;
      //       console.log("--setInterval" + outTime);
      //     }
      //   }, 500);
    },
    exportExcel() {
      /* generate workbook object from table */
      var wb = XLSX.utils.table_to_book(document.querySelector("#out-table"));
      /* get binary string as output */
      var wbout = XLSX.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array"
      });
      try {
        FileSaver.saveAs(
          new Blob([wbout], { type: "application/octet-stream" }),
          "sheetjs.xlsx"
        );
      } catch (e) {
        if (typeof console !== "undefined") console.log(e, wbout);
      }
      return wbout;
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    sendFetch() {
      var xml = new XMLHttpRequest();
      xml.open("get", "url?username=" + "username");
      xml.send();
      xml.onreadystatechange = function() {
        if (xml.status === 200 && xml.readyState === 4) {
          console.log(responseText);
        }
      };
    },
    sumbit() {
      /* Vuex  */
      console.log(this.$store.getters.doneTodos);
      // console.log(this.$store.state.page.cookie);
      // this.$store.commit("submitCookie", "new userName");
      // console.log(this.$store.state.page.cookie);
      // console.log(this.$store.getters.pageInfo);
      // this.$store.commit("TOGGLE_BUTTON", "never show");
      // console.log(this.$store.getters.pageInfo);
      // this.$store.dispatch("toggleButton", "show yi show");
      // console.log(this.$store.getters.pageInfo);
    }
  }
};
</script>

<style scoped>
.login {
  width: calc(100% - 300px);
  height: 100%;
  /* background-color: rgba(153, 119, 255, 0.178); */
  /* background: url('../assets/bg.jpg') no-repeat;
    background-size: 100%; */
  position: fixed;
  /* opacity: 0.2; */
  transition: all 2s linear;
}
.login:hover {
  /* opacity: 1; */
}
.login-input {
  width: 420px;
  /* height: 220px; */
  margin: 200px auto;
  border: 1px solid #b8b8b8;
  box-shadow: 0 0 2px 1px skyblue;
  border-radius: 15px;
  padding: 20px 0;
}
.login-title {
  width: 100%;
  margin-bottom: 20px;
}
.el-input {
  width: 320px;
  margin-right: 30px;
}
.el-form-item {
  width: 100%;
  height: 60px;
}
.login-btn {
  width: 320px;
  margin-right: 30px;
}
.el-menu {
  border: none;
}

.inp {
  color: #ff0603;
}
#loginCard {
  width: 500px;
  height: 300px;
  margin: 0 auto;
  padding-top: 20px;
  background-color: rgba(135, 207, 235, 0.342);
}
#loginCard .loginForm {
  width: 500px;
  height: 300px;
}
/* #loginCard p {
  margin-bottom: 20px;
} */
</style>



