<template>
  <div>
    <dv-full-screen-container class="box" v-if="!show">
      <dv-border-box-1 :color="['#0495C1', '#2563A2']" class="box-border1"
        >dv-border-box-1</dv-border-box-1
      >
      <dv-border-box-2 :color="['#0495C1', '#2563A2']" class="box-border1"
        >dv-border-box-2</dv-border-box-2
      >
      <dv-border-box-3 class="box-border1">dv-border-box-3</dv-border-box-3>
      <dv-border-box-6 :color="['#0495C1', '#2563A2']" class="box-border1"
        ><dv-loading>Loading...</dv-loading></dv-border-box-6
      >
      <div class="decoration1"><dv-decoration-5 /></div>
      <dv-border-box-4 :reverse="true" class="box-border1"
        >dv-border-box-4</dv-border-box-4
      >
      <div id="top-header">
        <dv-decoration-8 class="header-left-decoration" />
        <dv-decoration-5 class="header-center-decoration" />
        <dv-decoration-8 class="header-right-decoration" :reverse="true" />
        <div class="center-title"></div>
      </div>
      <dv-loading>Loading...</dv-loading>
    </dv-full-screen-container>

    <div class="big-box" v-if="show">
      <div class="box-border box1"></div>
      <div class="box-border box2"></div>
      <div class="box-border box3"></div>
      <div class="box-border box4"></div>
    </div>
    <div class="line">
      <div class="line-ball"></div>
      <div class="line-wire line-wire1"></div>
      <div class="line-wire line-wire2"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "datav",
  data() {
    return {
      show: false
    };
  }
};
</script>

<style>
.box {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 1500px;
  background: #282c34;
}
.box-border1 {
  width: 100%;
  height: 600px;
}
.decoration1 {
  width: 300px;
  height: 60px;
  box-sizing: border-box;
  margin: 10px auto;
  font-size: 18px;
  color: #35e1f6;
}
#top-header {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  flex-shrink: 0;
}
.header-center-decoration {
  width: 40%;
  height: 60px;
  margin-top: 30px;
}

.header-left-decoration,
.header-right-decoration {
  width: 25%;
  height: 60px;
}

.center-title {
  position: absolute;
  font-size: 30px;
  font-weight: bold;
  left: 50%;
  top: 15px;
  transform: translateX(-50%);
}

.big-box {
  position: relative;
  width: 300px;
  height: 600px;
  margin: 30px;
  /* transform: rotateX(270deg); */
}
.box-border {
  width: 20px;
  height: 20px;
  border-top: 2px solid skyblue;
  border-right: 2px solid skyblue;
  box-sizing: border-box;
  position: absolute;
  left: 50px;
}
.box1 {
  transform: rotateZ(-45deg);
}
.box2 {
  transform: rotateZ(135deg);
  top: 10px;
}
.box3 {
  transform: rotateZ(45deg);
}
.box4 {
  transform: rotateZ(-135deg);
}

.line {
  width: 300px;
  height: 20px;
  position: relative;
  top: 30px;
  left: 30px;
}
.line-wire {
  width: 300px;
  height: 20px;
  border-top: 2px solid #b8b8b8;
  position: absolute;
}
.line-wire1 {
  animation: moveing2 5s linear infinite;
}
.line-wire2 {
  left: 305px;
  animation: moveing2 5s linear 1.5s infinite;
}
.line-ball {
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background: red;
  z-index: 500;
  box-shadow: 2px 0 2px inset rgb(135, 145, 235);
  position: absolute;
  top: -2.5px;
  left: 600px;
  animation: moveing 5s ease-in-out infinite;
}
@keyframes moveing {
  0% {
    transform: translateX(-600px);
  }
  50% {
    transform: translateX(-300px);
  }
  55% {
    transform: translateX(-300px);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes moveing2 {
  0% {
    border-top: 2px solid #b8b8b8;
  }
  100% {
    border-top: 2px solid skyblue;
  }
}
</style>



