<template>
  <!-- WebSocketdemo  -->
  <div class="test">
    <el-input style="width:150px;" v-model="sendMeg"></el-input>
    <el-button type="primary" @click="websocketonopen"></el-button>
    <p>
      <span style="color:hotpink;">{{ receiveData }}</span>
    </p>
    <div v-if="show" style="width:200px;margin-left:20px;">
      <el-progress
        :text-inside="true"
        :stroke-width="16"
        :percentage="30"
      ></el-progress>
      <el-badge :value="12" class="item">
        <el-button type="primary" round size="small"></el-button>
      </el-badge>
    </div>
    <el-row style="height:30px;">
      <el-col :push="6" :span="6">
        <el-row>
          <el-col :span="3">
            <el-button type="primary" size="mini" @click="reduce">-</el-button>
          </el-col>
          <el-col style="line-height:30px;" :span="3">
            {{ count }}
          </el-col>
          <el-col :span="3">
            <el-button type="primary" size="mini" @click="add">+</el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "test",
  data() {
    return {
      show: false,
      websock: null,
      sendMeg: "",
      receiveData: "",
      num: 1
    };
  },
  computed: {
    count() {
      return this.num;
    }
  },
  created() {
    this.initWebSocket();
  },
  destroyed() {
    this.websock.close(); //websocket
  },
  methods: {
    add() {
      this.num += 1;
    },
    reduce() {
      this.num -= 1;
    },
    initWebSocket() {
      //weosocket
      const wsuri = "ws://123.207.136.134:9010/ajaxchattest"; //
      this.websock = new WebSocket(wsuri);
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onopen = this.websocketonopen;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketclose;
    },
    websocketonopen() {
      //send
      let actions = { test: "12345" };
      this.websocketsend(this.sendMeg); //JSON.stringify(actions)
    },
    websocketonerror() {
      //
      this.initWebSocket();
    },
    websocketonmessage(e) {
      //
      // const redata = JSON.parse(e.data);
      this.receiveData = e.data.split("[")[0];
    },
    websocketsend(Data) {
      //
      this.websock.send(Data);
    },
    websocketclose(e) {
      //
      console.log("", e);
      /* */
    }
  }
};
</script>
<style scoped>
.test {
  margin-top: 20px;
}
.test /deep/ .el-progress-bar__outer {
  background-color: #c5c5c5;
}
.bg-purple {
  background-color: skyblue;
  overflow: hidden;
}
</style>



