import Vue from "vue";
import App from "./App";
import router from "./router";
import Element from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
// import "../theme/index.css";
// import "@/assets/my-theme/index.css"; //
import store from "@/store/index";
import global from "@/utils/globalComponent";
import { checkJurisdiction } from "@/utils/jurisdiction";
import axios from "axios";
import "./utils/mock";
Vue.use(Element);
Vue.config.productionTip = false;
Vue.prototype.$http = axios;
import dataV from "@jiaminghi/data-view";

Vue.use(dataV);
//  VUE
Vue.directive("permission", {
  inserted(el, binding) {
    // inserted  
    let permission = binding.value; //  v-permission
    if (permission) {
      let hasPermission = checkJurisdiction(permission);
      if (!hasPermission) {
        //  Dom npm i sass-loader node-sass
        el.parentNode && el.parentNode.removeChild(el);
      }
    } else {
      throw new Error("key");
    }
  }
});
// 
function hasPermission(roles, permissionRoles) {
  if (roles.indexOf("admin") >= 0) return true; // admin permission passed directly
  if (!permissionRoles) return true;
  return roles.some(role => permissionRoles.indexOf(role) >= 0);
}
const whiteList = ["/login"]; // 

