// common/jurisdiction.js  /

export function checkJurisdiction(key) {
  // 
  let jurisdictionList = ["1", "2", "3", "5"];
  let index = jurisdictionList.indexOf(key);
  //   console.log("index:", index);
  if (index > -1) {
    // 
    return true;
  } else {
    // 
    return false;
  }
}



