const page = {
  state: {
    name: "pageName",
    count: "150",
    nums: "nums",
    filename: "",
    pageInfo: "string",
    cookie: "userName"
  },
  mutations: {
    TOGGLE_PAGE: (state, newMame) => {
      state.name = newMame;
    },
    CHECK_NUMS: (state, newNums) => {
      state.nums = newNums;
    },
    UESR_TOKEN: (state, filename) => {
      state.filename = filename;
    },
    TOGGLE_BUTTON: (state, data) => {
      state.pageInfo = data;
    },
    submitCookie: (state, data) => {
      state.cookie = data;
    }
  },
  actions: {
    TogglePage: ({ commit }, page) => {
      commit("TOGGLE_PAGE", page);
    },
    Checknums: (context, nums) => {
      context.commit("CHECK_NUMS", nums);
    },
    Usertoken: ({ commit }, token) => {
      commit("UESR_TOKEN", token);
    },
    toggleButton({ commit }, button) {
      commit("TOGGLE_BUTTON", button);
    }
  }
};

export default page;



