import Vue from "vue";
import Vuex from "vuex";
import getters from "./getters";
import page from "./moudle/page";

Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {
    page
  },
  state: {
    todos: [
      { id: 1, text: "...", done: true },
      { id: 2, text: "...", done: false }
    ]
  },
  getters
  //   getters: {
  //     doneTodos: state => {
  //       return state.todos.filter(todo => todo.done);
  //     }
  //   }
});

export default store;



