import Vue from "vue";
import Router from "vue-router";
// import express from "express";
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/login",
      name: "login",
      component: () => import("@/components/login")
    },
    {
      path: "/",
      name: "home",
      meta: {
        roles: ["admin", "editor"],
        requireAuth: false
      },
      component: () => import("@/components/home")
    },
    {
      path: "/shake",
      name: "shake",
      component: () => import("@/components/shake")
    },
    {
      path: "/datav",
      name: "datav",
      component: () => import("@/components/datav")
    },
    {
      path: "/admin",
      name: "Admin",
      component: () => import("@/pages/admin/Index"),
      children: [
        {
          path: "new",
          name: "New",
          component: () => import("@/pages/admin/New")
        },
        {
          path: "products",
          name: "",
          component: () => import("@/pages/admin/Products")
        },
        {
          path: "edit/:id",
          name: "Edit",
          component: () => import("@/pages/admin/Edit")
        }
      ]
    },
    {
      path: "/cart",
      name: "Cart",
      meta: {
        roles: ["admin"],
        requireAuth: false
      },
      component: () => import("@/pages/cart")
    },
    {
      path: "/header",
      name: "Header",
      component: () => import("@/pages/header")
    },
    {
      path: "/bianliang",
      name: "bianliang",
      component: () => import("@/pages/bianliang")
    },
    {
      path: "/scrollDigital",
      name: "scrollDigital",
      meta: {
        roles: ["admin", "editor"],
        requireAuth: false
      },
      component: () => import("@/components/scrollDigital")
    }
  ]
});

