const DEFAULT_THEME = {
  primary: "#2F54EB",
  subPrimary: "#D6E4FF",
  error: "#F5222D",
  success: "#52C41A",
  warning: "#FAAD14",
  background: "#FFFFFF",
  text: "#222222"
};
export default {
  DEFAULT: DEFAULT_THEME,
  FIRST: {
    ...DEFAULT_THEME,
    background: "#2590ff"
  }
};



