<template>
  <div id="app">
    <nav v-if="show">
      <div class="container">
        <ul class="nav-left">
          <li>
            <router-link to="/">Home</router-link>
          </li>
          <li>
            <router-link to="/admin">Admin</router-link>
          </li>
          <li>
            <router-link to="/cart">Cart</router-link>
          </li>
          <li>
            <router-link to="/login">login</router-link>
          </li>
        </ul>
      </div>
    </nav>
    <!-- <Button style="margin-left:30px;" v-permission="'5'" type='success' text='button1' @myClick="submit"></Button>
     <Button style="margin-right:30px;" v-permission="'10'" type='danger' text='button2' ></Button> -->

    <router-view />
    <canvas id="myCanvas" width="600" height="800">canvas</canvas>
    <!-- <el-button size="middle" type="primary" @click="pushPage"
      >pushPage</el-button> -->

    <!--  -->
    <div v-if="show">
      <el-date-picker
        v-model="value1"
        type="daterange"
        range-separator=""
        start-placeholder=""
        end-placeholder=""
        format="yyyyMMdd"
      >
      </el-date-picker>
      <button type="primary" @click="lookTime"></button>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      value1: "",
      show: false
    };
  },
  methods: {
    lookTime() {
      //new Date(etime).getTime() 
      console.log("" + new Date(this.value1[0]).getTime());
      console.log("" + new Date(this.value1[1]).getTime());
    },
    submit(e) {
      var e = e || event;
      console.log(e.target.parentNode);
      // e.target.parentNode && e.target.parentNode.remove(e.target)
    },
    drawCanvas() {
      //canvas 
      let myCanvas = document.getElementById("myCanvas");
      if (!myCanvas.getContext) alert("canvas");
      let ctx = myCanvas.getContext("2d");
      // ctx.fillRect( 0 , 0 , 300 , 300)
      var offset = 0;
      function draw() {
        offset++;
        if (offset > 16) {
          offset = 0;
        }
        ctx.clearRect(0, 0, myCanvas.width, myCanvas.height);
        ctx.setLineDash([6, 2]);
        ctx.lineDashOffset = -offset;
        ctx.strokeRect(10, 10, 100, 100);
      }
      setInterval(draw, 20);
    },
    pushPage() {
      this.$store.dispatch("TogglePage", "aaaa");
      this.$store.dispatch("Checknums", "new checknums22");
      // console.log(this.$store.state.page.nums);
    },
    colorChange(hex, opacity) {
      /**
       * 16rgba
       * @param {String} hex
       * @param {Number} opacity
       */
      if (!hex) hex = "#2c4dae";
      return (
        "rgba(" +
        parseInt("0x" + hex.slice(1, 3)) +
        "," +
        parseInt("0x" + hex.slice(3, 5)) +
        "," +
        parseInt("0x" + hex.slice(5, 7)) +
        "," +
        (opacity || "1") +
        ")"
      );
    },
    getAjax() {
      let xml = new XMLHttpRequest();
      xml.open("post", "url");
      xml.setRequestHeader("content-text", "application/json");
      xml.send();
      xml.onreadystatechange = function() {
        if (xml.status === 200 && xml.readyState == 4) {
          console.log(xml.responseText);
        }
      };
    }
  },
  mounted() {
    // this.drawCanvas();
    // getters
    // console.log(this.$store.state.page.name)
    // console.log(this.$store.state.page.nums);
    // console.log(this.$store.getters.name);
  }
};
</script>

<style>
html,
body {
  width: 100%;
  height: 100%;
}
html {
  font-size: 16px;
}
* {
  margin: 0;
  padding: 0;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
}
</style>



